#newPostPage {
  padding-top: 5vh;
  width: 70vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 0 -2vw;
}

#newPostWrapper {
  width: 55vw;
  padding: 3vh;
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#newPostWrapper form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#newPostWrapper h2 {
  color: #24304b;
  width: 50vw;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 3px;
  font-size: 1.7vmax;
  text-align: left;
  margin: 0 0 2vh 0;
}

#newPostWrapper input {
  width: 50vw;
  line-height: 1.5em;
  font-size: 1em;
  margin: 0.5vh 0 0.5vh 0;
}

#newPostWrapper textarea {
  margin: 0.5vh 0 0.5vh 0;
  width: 50vw;
  height: 40vh;
  line-height: 1.5em;
  font-size: 1em;
  font-family: Arial, Helvetica, sans-serif;
}

#newPostWrapper button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1em;
}

#imgInput,
#titleInput,
#textBodyInput,
#tagsInput,
#newPostWrapper button {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  margin: 0.5vh;
}
